.alert-container {
    position: fixed;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    width: 350px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 1000;
  }
  
  .alert {
    padding: 12px;
    border-radius: 5px;
    font-size: 14px;
    color: #155724;
    background: #d4edda;
    border-left: 5px solid #28a745;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
  }
  
  .alert-success {
    /* background: #d4edda; */
    background-color: rgb(240 253 244);
    color: rgb(35, 173, 86);
    border-left: 4px solid rgb(88, 211, 133);;
  }
  
  /* Warning Alert */
  .alert-warning {
    background: #fff3cd;
    color: #856404;
    border-left: 5px solid #ffc107;
  }
  
  /* Error Alert */
  .alert-error {
    background: #f8d7da;
    color: #721c24;
    border-left: 5px solid #dc3545;
  }
  
  /* Apply fade-out effect */
  .fade-out {
    opacity: 0;
  }